body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-size: 14px;
  background-color: #eee;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  padding-bottom: 100px;
}
.main{
  margin-left: auto;
  margin-right: auto;
}
.header{
  justify-content: center;
  border-radius: 15px;
  background-color: white;
  box-shadow: 5px 10px 20px #d8d8d8;
  padding: 30px;
}
.filter{
  align-items: center;
}
.cards{
  display: flex;
  justify-content: center;
  margin-top: 5vh;
}
.cart{
  margin: 20px;
  align-items: flex-end;
  display: flex;
  position: absolute;
}
.cart-num{
  background-color: rgb(255, 149, 0);
  padding: 5px;
  border-radius: 50%;
}
select{
  border: none;
  background-color: blue;
  background: none;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.card-bg{
    margin: 20px;
    border-radius: 15px;
    background-color: white;
    box-shadow: 5px 10px 20px #d8d8d8;
    height: 50vh;
    width: 30vh;
    padding: 30px 10px;
    text-align: center;
}

img{
    margin: 5%;
    width: 60%;
    height: 45%;
    object-fit: cover;
    overflow: hidden;
}
p{
    font-size: 14px;
}
.card-name{
    width: 30vh;
    height: 5vh;
}
.price{
    font-weight: 700;
}
.order{
    position: fixed;
}
button{
    padding: 12px 20px;
    color: white;
    font-size: 12px;
    font-weight: 700;
    background-color: rgb(114, 151, 237);
    border: none;
    border-radius: 12px;
}

button:hover{
    background-color: rgb(183, 93, 243) ;
    cursor: pointer;
    transition-duration: 0.5s;
}


.cart-card{
    margin: 5vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cart-card img{
    object-fit: cover;
    width: 10vh;
    border-radius: 12px;
    margin: 10px;
}
.cart-name{
    width: 30%;
}
.cart-price{
    padding: 10px;
    background-color: white;
    border: none;
    border-radius: 12px;
    font-weight: 700;
}
.tag{
    height: 2vh;
}
